/* my custom css reset then normalize.css */
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: currentColor;
}

li {
  list-style: none;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

h1,
h2 {
  margin: 0;
}

